/*To ensure on laptops that the sheep are not submerged into the ground*/
@media screen and (min-width: 901px) and (max-width: 1600px) {
  #swaledaleSheep1 {
    -webkit-transform: translate(-200px, 0px);
    -moz-transform: translate(-200px, 0px);
    -ms-transform: translate(-200px, 0px);
    -o-transform: translate(-200px, 0px);
    transform: translate(-200px, 0px);
    overflow: visible;
  }

  #swaledaleSheep2 {
    -webkit-transform: translate(-600px, 0px);
    -moz-transform: translate(-600px, 0px);
    -ms-transform: translate(-600px, 0px);
    -o-transform: translate(-600px, 0px);
    transform: translate(-600px, 0px);
    overflow: visible;
  }

  #lamb1 {
    -webkit-transform: translate(-200px, -20px);
    -moz-transform: translate(-200px, -20px);
    -ms-transform: translate(-200px, -20px);
    -o-transform: translate(-200px, -20px);
    transform: translate(-200px, -20px);
    overflow: visible;
  }

  #lamb2 {
    -webkit-transform: translate(-200px, -20px);
    -moz-transform: translate(-200px, -20px);
    -ms-transform: translate(-200px, -20px);
    -o-transform: translate(-200px, -20px);
    transform: translate(-200px, -20px);
    overflow: visible;
  }

  .April2025header {
    height: 90vh;
  }

  #ground {
    width: 400vw;
    x: -50px;
    y: 480px;
    height: 400px;
  }
}

/*To ensure on ipad screens and larger mobiles that photos are displayed in columnar format to ensure sizing and clarity of image optimal*/
@media screen and (min-width: 480px) and (max-width: 900px) {
  /*To ensure on ipad screens and larger mobiles (min-width 480px, max-width 900px) that the ground extends to meet the upper margin of the darker body colour */
  #groundApril2025 {
    width: 400vw;
    x: -50px;
    y: 480px;
    height: 2500px;
  }

  /*To ensure on ipad screens and larger mobiles (min-width 480px, max-width 900px) that the sheep and lambs are correctly positioned*/
  #swaledaleSheep1 {
    -webkit-transform: translate(-200px, 0px);
    -moz-transform: translate(-200px, 0px);
    -ms-transform: translate(-200px, 0px);
    -o-transform: translate(-200px, 0px);
    transform: translate(-200px, 0px);
    overflow: visible;
  }

  #swaledaleSheep2 {
    -webkit-transform: translate(-600px, 0px);
    -moz-transform: translate(-600px, 0px);
    -ms-transform: translate(-600px, 0px);
    -o-transform: translate(-600px, 0px);
    transform: translate(-600px, 0px);
    overflow: visible;
  }

  #lamb1 {
    -webkit-transform: rotate(-30, 530, 485);
    -moz-transform: rotate(-30, 530, 485);
    -ms-transform: rotate(-30, 530, 485);
    -o-transform: rotate(-30, 530, 485);
    transform: translate(-200px, -50px);
    overflow: visible;
  }

  #lambtail1 {
    -webkit-transform: translate(5px, 10px);
    -moz-transform: translate(5px, 10px);
    -ms-transform: translate(5px, 10px);
    -o-transform: translate(5px, 10px);
    transform: translate(5px, 10px);
  }

  #lambtail2 {
    -webkit-transform: translate(0px, -10px);
    -moz-transform: translate(0px, -10px);
    -ms-transform: translate(0px, -10px);
    -o-transform: translate(0px, -10px);
    transform: translate(0px, -10px);
  }

  #lamb2 {
    -webkit-transform: translate(-200px, -40px);
    -moz-transform: translate(-200px, -40px);
    -ms-transform: translate(-200px, -40px);
    -o-transform: translate(-200px, -40px);
    transform: translate(-200px, -40px);
    overflow: visible;
  }

  .blogPhotoDivApril2025 {
    display: flex;
    flex-direction: column;
    background-color: #f6f0a3;
    padding-bottom: 3%;
    padding-top: 2%;
    margin-top: 2%;
  }
}

/*To ensure on devices  (min-width 598px, max-width 699px) that the April2025header height is correct size*/
.April2025header {
  height: 90vh;
}

@media screen and (max-width: 480px) {
  /*To ensure on mobile screens below 480px width (this includes one plus phones) that relevant components of the site are displayed with the best dimensions for the smaller devices*/

  .mountainForeground,
  .mountainMiddle,
  .mountainBackground {
    -webkit-transform: scale(50%) translate(-150px, 0px);
    -moz-transform: scale(50%) translate(-150px, 0px);
    -ms-transform: scale(50%) translate(-150px, 0px);
    -o-transform: scale(50%) translate(-150px, 0px);
    transform: scale(50%) translate(-150px, 0px);
  }

  #gradSpringSun {
    -webkit-transform: translateY(-300px);
    -moz-transform: translateY(-300px);
    -ms-transform: translateY(-300px);
    -o-transform: translateY(-300px);
    transform: translateY(-300px);
  }

  #groundApril2025 {
    width: 400vw;
    x: -50px;
    y: 260px;
    height: 2500px;
  }

  .April2025header {
    height: 50vh;
  }

  #swaledaleSheep1 {
