    -moz-transform: scale(50%) translate(-200px, 0px);
    -ms-transform: scale(50%) translate(-200px, 0px);
    -o-transform: scale(50%) translate(-200px, 0px);
    transform: scale(50%) translate(-200px, 0px);
    overflow: visible;
  }

  #swaledaleSheep2 {
    -webkit-transform: scale(50%) translate(-600px, 0px);
    -moz-transform: scale(50%) translate(-600px, 0px);
    -ms-transform: scale(50%) translate(-600px, 0px);
    -o-transform: scale(50%) translate(-600px, 0px);
    transform: scale(50%) translate(-600px, 0px);
    overflow: visible;
  }

  #lamb1 {
    -webkit-transform: scale(60%) translate(-200px, 0px);
    -moz-transform: scale(60%) translate(-200px, 0px);
    -ms-transform: scale(60%) translate(-200px, 0px);
    -o-transform: scale(60%) translate(-200px, 0px);
    transform: scale(60%) translate(-200px, 0px);
    overflow: visible;
  }

  #lamb2 {
    -webkit-transform: scale(60%) translate(-200px, 0px);
    -moz-transform: scale(60%) translate(-200px, 0px);
    -ms-transform: scale(60%) translate(-200px, 0px);
    -o-transform: scale(60%) translate(-200px, 0px);
