/*css to run rain and cloud animation*/

.main {
  display: flex;
  flex-direction: column;
  width: 50vw;
  height: auto;
  margin-bottom: 10%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  /* overflow: hidden; */
}

h2 {
  text-align: center;
}

ul li {
  display: block;
}

.cloud {
  display: inline-block;
  background: white;
  width: 120px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  top: -30px;
  filter: blur(6px);
  z-index: 11;
  left: -50px;
  opacity: 0.76;
  zoom: 0.4;
}

.cloud:before {
  content: "";
  display: inline-block;
  background: white;
  width: 100px;
  height: 100px;
  filter: blur(3px);
  position: relative;
  border-radius: 50%;
  top: -30px;
  left: 60px;
}

.cloud:after {
  content: "";
  display: inline-block;
  background: white;
  width: 200px;
  height: 100px;
  filter: blur(3px);
  position: relative;
  border-radius: 50%;
  top: -80px;
  left: 70px;
}

.cloud:nth-child(1) {
  top: 70px;
  left: -50px;
}
.cloud:nth-child(2) {
  top: 70px;
  left: 8px;
}
.cloud:nth-child(3) {
  top: 50px;
  left: 180px;
}

.cloud:nth-child(4) {
  top: 50px;
  left: 230px;
}

@keyframes cloudAnimation {
  0% {
    transform: translate(-500px, 0);
  }
  100% {
    -webkit-transform: translate(1000px, 0);
    -moz-transform: translate(1000px, 0);
    -ms-transform: translate(1000px, 0);
    -o-transform: translate(1000px, 0);
    transform: translate(1000px, 0);
  }
}
.allcloud {
  -webkit-animation: 11000ms cloudAnimation linear infinite;
  animation: 11000ms cloudAnimation linear infinite;
}
.dropWater {
  position: absolute;
  z-index: 20;
  left: 284px;
  bottom: 0px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #7fc1f9;
  -webkit-animation: drop 0.8s linear 0.44s infinite;
  animation: drop 0.8s linear 0.44s infinite;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  transform-origin: 50% 100%;
}
.dropWater:before {
  position: absolute;
  content: "";
  display: block;
  top: -2px;
  left: 1px;
  width: 6px;
  height: 6px;
  background: #7fc1f9;
  border-radius: 3px;
}
.dropWater:after {
  position: absolute;
  content: "";
  display: block;
  top: -5px;
  left: 2px;
  width: 4px;
  height: 10px;
  background: #7fc1f9;
  border-radius: 50%;
}
@keyframes drop {
  0% {
    transform: translate3d(40px, -320px, 0) scaleX(1) scaleY(1) rotate(17deg);
  }
  85% {
    transform: translate3d(0, 0, 0) scaleX(1) scaleY(1) rotate(17deg);
  }
  100% {
    transform: translate3d(0, 0, 0) scaleX(3) scaleY(0) rotate(0deg);
  }
}
