/*To ensure on mobile screens below 902px width (this includes one plus phones) that relevant components of 
        the site are displayed with the best dimensions and appearance for the smaller devices*/
@media screen and (min-width: 481) and (max-width: 902px) {
  .slideshowentryCdS {
    display: block;
    margin-left: auto;
    margin-right: auto;
    pointer-events: none;
    height: auto;
    width: 50%;
    border: solid navy 4px;
    margin-top: 1%;
    margin-bottom: 1%;
    -webkit-border-radius: 70%;
    -moz-border-radius: 70%;
    -ms-border-radius: 70%;
    -o-border-radius: 70%;
    padding: 5%;
    border-radius: 70%;
  }

  .blogPhotoDivCdS {
    display: flex;
    flex-direction: column;
    background-color: #0000ff;
    padding-bottom: 15%;
    padding-top: 5%;
    margin-top: 2%;
  }

  .CdSIntroPhoto {
    margin-left: auto;
    margin-right: auto;
    pointer-events: none;
    width: 65%;
    height: auto;
    border: solid #0000ff 8px;
    margin-top: 1%;
    margin-bottom: 1%;
    -webkit-border-radius: 80%;
    -moz-border-radius: 80%;
    -ms-border-radius: 80%;
    -o-border-radius: 80%;
    border-radius: 80%;
  }
}

@media screen and (min-width: 402px) and (max-width: 900px) {
  .blogPhotoDivCdS {
    display: flex;
    flex-direction: column;
    background-color: #ffd700;
    opacity: 0.9;
    padding-bottom: 15%;
    padding-top: 5%;
    margin-top: 2%;
    -webkit-transform: size(1.2);
    -moz-transform: size(1.2);
    -ms-transform: size(1.2);
    -o-transform: size(1.2);
    transform: size(1.2);
  }

  .slideshowentryCdS {
    display: block;
    margin-left: auto;
    margin-right: auto;
    pointer-events: none;
    height: auto;
    width: 70%;
    border: solid navy 4px;
    margin-top: 1%;
    margin-bottom: 1%;
    -webkit-border-radius: 70%;
    -moz-border-radius: 70%;
    -ms-border-radius: 70%;
    -o-border-radius: 70%;
    padding: 5%;
    border-radius: 70%;
  }
}

