.fireplace {
  width: 320px;
  height: 320px;
  position: relative;
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;  */
  margin-top: 25%;
  margin-left: 40vw;

  .fireplaceFlame,
  .fireplaceFlameBig {
    height: 500px;
    width: 180px;
    background: linear-gradient(
      0deg,
      rgba(236, 221, 66, 1) 10%,
      rgba(237, 174, 52, 1) 15%,
      rgba(237, 100, 52, 1) 50%,
      rgba(250, 71, 8, 1) 59%
    );
    /* background:black; */
    position: absolute;
    margin-bottom: 30px;
    z-index: 3;
    opacity: 0.7;
  }

  .fireplaceFlame {
    width: 350px;
    margin-left: 10px;
    -webkit-animation: burn 1.5s linear forwards infinite;
    animation: burn 1.5s linear forwards infinite;
  }

  .fireplaceFlameBig {
    margin-bottom: -5px;
    z-index: -1;
    opacity: 1;
    animation: burn_alt 2.5s linear forwards infinite;
  }

  .fireplaceLog {
    background: linear-gradient(#e66465, #5d5e55);
    height: 200px;
    width: 20px;
    position: absolute;
    border-radius: 10px;
    transform-origin: bottom center;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);
    margin-top: 90%;
    margin-left: -10%;
    margin-right: auto;
  }

  .fireplaceLog:after {
    content: "";
    display: block;
    background: #ff915b;
    width: 100%;
    height: 20px;
    border-radius: 20px;
    transform-origin: top center;
    transform: scale(1, 0.5);
  }

  section:nth-of-type(1) {
    left: 70px;
    transform: rotateZ(45deg);
  }

  section:nth-of-type(2) {
    left: 100px;
    transform: rotateZ(25deg);
  }

  section:nth-of-type(3) {
    left: 140px;
    transform: rotateZ(5deg);
  }

  section:nth-of-type(4) {
    left: 140px;
    transform: rotateZ(-5deg);
  }

  section:nth-of-type(5) {
    left: 170px;
    transform: rotateZ(-15deg);
  }

  section:nth-of-type(6) {
    left: 200px;
    transform: rotateZ(-35deg);
  }

  section:nth-of-type(7) {
    left: 230px;
    transform: rotateZ(-45deg);
  }

  .fireplaceSpark {
    position: absolute;
    height: 4%;
    width: 4px;
    border-radius: 1px;
    top: 0;
    left: 0;
    filter: blur(1px);
    background: yellow;
    z-index: 10;
    opacity: 0;
  }

  main:nth-of-type(1) {
    -webkit-animation: fireplaceSpark_1 1s forwards infinite;
    animation: fireplaceSpark_1 1s forwards infinite;
  }
  main:nth-of-type(2) {
    -webkit-animation: fireplaceSpark_2 1s 0.75s forwards infinite;
    animation: fireplaceSpark_2 1s 0.75s forwards infinite;
  }

  main:nth-of-type(3) {
    -webkit-animation: fireplaceSpark_3 1s 0.25s forwards infinite;
    animation: fireplaceSpark_3 1s 0.25s forwards infinite;
  }

  main:nth-of-type(4) {
    -webkit-animation: fireplaceSpark_4 1s 0.5s forwards infinite;
    animation: fireplaceSpark_4 1s 0.5s forwards infinite;
  }

  .fireplaceLight {
    height: 100%;
    width: 100%;
    border-radius: 50% 50% 30% 30%;
    transform: scale(1.1, 1.2);
    filter: blur(50px);
    background: orange;
    position: absolute;
    top: 40px;
    left: 0;
    z-index: -1;
    opacity: 0.4;
  }

  .blur {
    filter: blur(1px);

    .blurFix {
      position: absolute;
    }
  }
}

