/*CSS for Harry's PhD congratulations firework display */

.pyro {
  -webkit-transform: scale(90%) translate(0px, 150px);
  -moz-transform: scale(90%) translate(0px, 150px);
  -ms-transform: scale(90%) translate(0px, 150px);
  -o-transform: scale(90%) translate(0px, 150px);
  transform: scale(90%) translate(0px, 150px);
}

.CongratulationsPhD {
  background-color: black;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 40%;
  padding-right: 2%;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
}

.CongratulationsPhD .pyro > .before,
.CongratulationsPhD .pyro > .after {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  box-shadow: 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff,
    0 0 #fff, 0 0 #fff, 0 0 #fff;
  animation: 1s bang ease-out infinite backwards,
    1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
}
.CongratulationsPhD .pyro > .after {
  animation-delay: 1.25s, 1.25s, 1.25s;
  animation-duration: 1.25s, 1.25s, 6.25s;
}
