/*CSS for Harry's rotating PhD congratulations message */
.CongratsMessagePhD {
  align-items: center;
  display: flex;
  height: 50vh;
  text-align: center;
}

.CongratsMessagePhD-content {
  position: relative;
  width: 100%;
}

/* @media (min-width: 768px) { 
  font-size: 18px;
} */

.CongratsMessagePhD-description {
  font-family: "Georgia", serif;
  font-size: 22px;
  font-weight: bold;
  margin: 0;
  color: #e7b75c;
}

.CongratsMessagePhD-main {
  font-family: "Georgia", sans-serif;
  font-weight: bold;
  font-size: 22px;
  left: 0;
  margin-bottom: 15%;
  margin-top: 15%;
  opacity: 0;
  position: absolute;
  right: 0;
  text-align: center;
  text-transform: uppercase;
  top: 0;
  color: #e7b75c;
  -webkit-animation: 8s rotate-text-up linear infinite;
  animation: 8s rotate-text-up linear infinite;

  @media (max-width: 480px) {
    font-size: 20px;
  }
}

@keyframes rotate-text-up {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 200px, 0);
    -moz-transform: translate3d(0, 200px, 0);
    -ms-transform: translate3d(0, 200px, 0);
    -o-transform: translate3d(0, 200px, 0);
    transform: translate3d(0, 200px, 0);
  }

  20%,
  80% {
    opacity: 1;
    -webkit-transform: translate3d(0, 50px, 0);
    -moz-transform: translate3d(0, 50px, 0);
    -ms-transform: translate3d(0, 50px, 0);
    -o-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(0, 20px, 0);
    -moz-transform: translate3d(0, 20px, 0);
    -ms-transform: translate3d(0, 20px, 0);
    -o-transform: translate3d(0, 20px, 0);
    transform: translate3d(0, 20px, 0);
  }
}

