/*Styling for animated cyclist display*/

.cloudCyclist {
  display: inline-block;
  background: white;
  width: 120px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  top: -30px;
  filter: blur(6px);
  z-index: 11;
  left: -50px;
  opacity: 0.76;
  zoom: 0.5;
  -webkit-transform: translateY(250px);
  -moz-transform: translateY(250px);
  -ms-transform: translateY(250px);
  -o-transform: translateY(250px);
  transform: translateY(250px);
}

.cloudCyclist:before {
  content: "";
  display: inline-block;
  background: white;
  width: 100px;
  height: 100px;
  filter: blur(3px);
  position: relative;
  border-radius: 50%;
  top: 0px;
  left: 70px;
}

.cloudCyclist:after {
  content: "";
  display: inline-block;
  background: white;
  width: 200px;
  height: 100px;
  filter: blur(3px);
  position: relative;
  border-radius: 50%;
  top: -80px;
  left: 60px;
}

.cloudCyclist:nth-child(1) {
  top: 120px;
  left: -50px;
}
.cloudCyclist:nth-child(2) {
  top: 120px;
  left: 8px;
}
.cloudCyclist:nth-child(3) {
  top: 190px;
  left: 180px;
}

.cloudCyclist:nth-child(4) {
  top: 190px;
  left: 280px;
}

.cloudCyclist:nth-child(5) {
  top: 100px;
  left: 480px;
}

.cloudCyclist:nth-child(6) {
  top: 100px;
  left: 580px;
}

.cloudCyclist:nth-child(7) {
  top: 70px;
  left: 780px;
}
.cloudCyclist:nth-child(8) {
  top: 200px;
  left: 880px;
}
.cloudCyclist:nth-child(9) {
  top: 200px;
  left: 1080px;
}

.cloudCyclist:nth-child(10) {
  top: 100px;
  left: 1180px;
}

.cloudCyclist:nth-child(11) {
  top: 150px;
  left: 1280px;
}

.cloudCyclist:nth-child(12) {
  top: 150px;
  left: 1380px;
}

@keyframes cloudAnimationCyclist {
  0% {
    -webkit-transform: translate(-1200px, 0);
    -moz-transform: translate(-1200px, 0);
    -ms-transform: translate(-1200px, 0);
    -o-transform: translate(-1200px, 0);
    transform: translate(-1200px, 0);
  }
  100% {
    -webkit-transform: translate(500px, 0);
    -moz-transform: translate(500px, 0);
    -ms-transform: translate(500px, 0);
    -o-transform: translate(500px, 0);
    transform: translate(500px, 0);
  }
}
.allcloudCyclist {
  -webkit-animation: 4000ms cloudAnimationCyclist linear reverse infinite;
  animation: 4000ms cloudAnimationCyclist linear reverse infinite;
}

