.deerAnimation {
  box-sizing: border-box;
  overflow: visible;
}

/*To ensure on all devices that the 3 deer are scaled and repositioned so visible*/
@media screen and (min-width: 1111px) and (max-width: 3500px) {
  .deer1 {
    -webkit-transform: translate(150px, 0px);
    -moz-transform: translate(150px, 0px);
    -ms-transform: translate(150px, 0px);
    -o-transform: translate(150px, 0px);
    transform: translate(150px, 0px);
  }
  .deer2 {
    -webkit-transform: translate(150px, -140px) scale(0.7);
    -moz-transform: translate(150px, -140px) scale(0.7);
    -ms-transform: translate(150px, -140px) scale(0.7);
    -o-transform: translate(150px, -140px) scale(0.7);
    transform: translate(150px, -140px) scale(0.7);
  }
}
@media screen and (min-width: 602px) and (max-width: 1100px) {
  .deer1 {
    -webkit-transform: translate(200px, 0px) scale(0.7);
    -moz-transform: translate(200px, 0px) scale(0.7);
    -ms-transform: translate(200px, 0px) scale(0.7);
    -o-transform: translate(200px, 0px) scale(0.7);
    transform: translate(200px, 0px) scale(0.7);
  }
  .deer2 {
    -webkit-transform: translate(-300px, 100px) scale(0.7);
    -moz-transform: translate(-300px, 100px) scale(0.7);
    -ms-transform: translate(-300px, 100px) scale(0.7);
    -o-transform: translate(-300px, 100px) scale(0.7);
    transform: translate(-300px, 100px) scale(0.7);
  }
  .deer3 {
    -webkit-transform: translate(0px, 0px) scale(0.8);
    -moz-transform: translate(0px, 0px) scale(0.8);
    -ms-transform: translate(0px, 0px) scale(0.8);
    -o-transform: translate(0px, 0px) scale(0.8);
    transform: translate(0px, 0px) scale(0.8);
  }
}

@media screen and (max-width: 601px) {
  .deer1 {
    -webkit-transform: translate(30px, 0px) scale(0.7);
    -moz-transform: translate(30px, 0px) scale(0.7);
    -ms-transform: translate(30px, 0px) scale(0.7);
    -o-transform: translate(30px, 0px) scale(0.7);
    transform: translate(30px, 0px) scale(0.7);
  }
  .deer2 {
    -webkit-transform: translate(-500px, 100px) scale(0.7);
    -moz-transform: translate(-500px, 100px) scale(0.7);
    -ms-transform: translate(-500px, 100px) scale(0.7);
    -o-transform: translate(-500px, 100px) scale(0.7);
    transform: translate(-500px, 100px) scale(0.7);
  }

  .deer3 {
    -webkit-transform: translate(-280px, 0px) scale(0.8);
    -moz-transform: translate(-280px, 0px) scale(0.8);
    -ms-transform: translate(-280px, 0px) scale(0.8);
    -o-transform: translate(-280px, 0px) scale(0.8);
    transform: translate(-280px, 0px) scale(0.8);
  }
}
