.backgroundMarch2025 {
  background-color: #57a788;
  padding-bottom: 50%;
  height: 100%;
}

.March2025Header {
  color: whitesmoke;
  display: block;
  text-align: center;
  justify-items: center;
  font-size: 1.55em;
  font-weight: bold;
  width: 100%;
  height: 20vh;
  position: relative;
  background-color: #816c66;
  padding: 4%;
}

* {
  box-sizing: border-box;
}

#backbuttonMarch2025 {
  display: block;
  position: absolute;
  height: fit-content;
  width: fit-content;
  padding: 2%;
  background-color: #f5f5f5;
  border: grey 2px solid;
  text-align: center;
  font-size: large;
  font-weight: bold;
  color: #636363;
  top: 10%;
  text-decoration: underline;
  margin: 1% 25% 10% 75%;
  -webkit-border-radius: 15%;
  -moz-border-radius: 15%;
  -ms-border-radius: 15%;
  -o-border-radius: 15%;
  border-radius: 15%;
}

.newsMarch2025 {
  color: whitesmoke;
  background-color: #816c66;
  text-align: left;
  padding: 50px 20px 25px 20px;
  margin-top: 3%;
  margin-bottom: 5%;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  font-weight: bold;
  width: 100%;
}

.dateAllMarch2025 {
  color: whitesmoke;
  /* border:solid 2px whitesmoke; */
  background-color: #816c66;
  text-align: center;
  padding: 10px 10px 10px 10px;
  margin-top: 20%;
  margin-bottom: 1%;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  font-weight: bold;
  width: 100%;
}

.dateMarch2025 {
  color: whitesmoke;
  /* border:solid 2px whitesmoke; */
  background-color: #816c66;
  text-align: center;
  padding: 10px 10px 10px 10px;
  margin-top: 2%;
  margin-bottom: 1%;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  font-weight: bold;
  width: 100%;
}

.newsheaderMarch2025 {
  color: whitesmoke;
  /* border:solid 2px whitesmoke; */
  background-color: #816c66;
  text-align: center;
  padding: 10px 20px 10px 20px;
  margin-top: 0.3%;
  margin-bottom: 2%;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  font-weight: bold;
  width: 100%;
}

.dateNewsMarch2025 {
  font-weight: bold;
  font-size: 1.2rem;
  color: #f7ca46;
}

.blogPhotoDivMarch2025 {
  display: flex;
  flex-direction: row;
  border: solid 2px #757c88;
  background-color: #816c66;
  padding-bottom: 2%;
  padding-top: 3%;
  padding-left: 2%;
  padding-right: 2%;
  margin-top: 5vh;
  margin-bottom: 5vh;
}

.blogPhotoMarch2025 img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  height: auto;
  border: solid whitesmoke 4px;
  -webkit-border-radius: 20%;
  -moz-border-radius: 20%;
  -ms-border-radius: 20%;
  -o-border-radius: 20%;
  border-radius: 20%;
}

.march2025FigCaption {
  text-align: center;
  font-family: bold;
  color: whitesmoke;
  font-size: 1.2rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  overflow-wrap: normal;
  padding-top: 5%;
}

.LinkMarch2025 {
  color: #ffd200;
  font-size: 1.2rem;
  text-decoration: underline;
}

.LinkMarchWalks2025 {
  color: #0000ee;
  font-size: 1.2rem;
  text-decoration: underline;
}

.slideshowinstructionsMarch2025 {
  margin-top: 1%;
  margin-bottom: 1%;
  font-size: 1.3rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
  display: block;
  color: whitesmoke;
  background-color: #816c66;
}

.slideshowentryMarch2025 {
  display: block;
  margin-left: auto;
  margin-right: auto;
  pointer-events: none;
  height: auto;
  width: 25%;
  border: solid whitesmoke 4px;
  margin-top: 1%;
  margin-bottom: 1%;
  -webkit-border-radius: 70%;
  -moz-border-radius: 70%;
  -ms-border-radius: 70%;
  -o-border-radius: 70%;
  border-radius: 70%;
}

.SlideshowHostPhotoMarch2025 {
  display: flex;
  flex-direction: column;
  border: solid 2px var(--whiteSmoke);
  background-color: #816c66;
  padding-bottom: 2%;
  padding-top: 2%;
  justify-content: center;
  text-align: center;
}

.datenewsDivMarch2025 {
  background-color: "#816c66";
  display: flex;
  flex-direction: column;
  margin-top: 0vh;
  margin-bottom: 0vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
}

.DailyNarrativeMarch2025 {
  background-color: #f2e3d6;
  text-align: left;
  padding-top: 2%;
  padding-bottom: 2%;
  padding-right: 10%;
  padding-left: 10%;
  color: whitesmoke;
  font-size: 1rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
}

.componentWalkDetailsMarch2025 {
  background-color: #f2e3d6;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 2%;
  padding-right: 2%;
  color: #636363;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  font-family: Calibri, Trebuchet MS, sans-serif;
}

.titlesMarch2025 {
  color: #636363;
  font-size: 1.2rem;
}

.headTitlesMarch2025 {
  color: #636363;
  font-size: 1.2rem;
  text-decoration: underline;
  padding-left: 10%;
}

@media screen and (min-width: 480px) and (max-width: 900px) {
  .blogPhotoDivMarch2025 {
    flex-direction: column;
    padding-bottom: 5%;
    padding-top: 5%;
    margin-top: 2%;
  }

  .slideshowentryMarch2025 {
    display: block;
    margin-left: auto;
    margin-right: auto;
    pointer-events: none;
    height: auto;
    width: 35%;
    border: solid var(--whiteSmoke) 3px;
    margin-top: 3%;
    margin-bottom: 5%;
  }
}

@media screen and (max-width: 480px) {
  /*To ensure on mobile screens below 480px width (this includes one plus phones) that relevant components of 
    the site are displayed with the best dimensions for the smaller devices*/
  .March2025header {
    display: block;
    text-align: center;
    font-size: 1.75em;
    font-weight: bold;
    width: 100vw;
    height: 15vh;
    position: relative;
    background-color: #816c66;
    overflow-x: hidden;
  }

  .dateMarch2025 {
    color: whitesmoke;
    border: solid 2px var(--whiteSmoke);
    background-color: #816c66;
    text-align: center;
    padding: 10px 10px 10px 10px;
    margin-top: 1%;
    margin-bottom: 1%;
    font-size: 1.2rem;
  }

  .newsMarch2025 {
    color: whitesmoke;
    border: solid 2px var(--whiteSmoke);
    background-color: #816c66;
    text-align: left;
    padding: 20px 10% 20px 10%;
    margin-top: 2%;
    margin-bottom: 0%;
    font-size: 1rem;
  }

  .blogPhotoDivMarch2025 {
    display: flex;
    flex-direction: column;
    background-color: #816c66;
    padding-bottom: 5%;
    padding-top: 5%;
    margin-top: 2%;
  }

  .slideshowinstructionsMarch2025 {
    margin-top: 1%;
    margin-bottom: 2%;
    padding-right: 10%;
    padding-left: 10%;
    font-size: 1.1rem;
    font-family: Calibri, Trebuchet MS, sans-serif;
    display: block;
    color: white;
  }

  .slideshowentryMarch2025 {
    width: 80%;
    border: solid var(--whiteSmoke) 3px;
    margin-top: 3%;
    margin-bottom: 5%;
  }

  .componentWalkDetailsMarch2025 {
    color: #636363;
  }

  .titlesMarch2025 {
    color: #636363;
  }

  .headTitlesMarch2025 {
    color: #636363;
  }

  .LinkMarchWalks2025 {
    color: #0000ee;
  }
}
